
.seller {
  position: fixed;
  z-index: 30;
  top: 174px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.seller .seller-content .overview {
  position: relative;
  padding: 18px;
}
.seller .seller-content .overview .overview-top {
  padding-bottom: 18px;
  position: relative;
}
.seller .seller-content .overview .overview-top:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  content: '';
  border-top: 1px solid rgba(7,17,27,0.1);
}
.seller .seller-content .overview .overview-top .name {
  margin: 0px 0px 8px 0px;
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
}
.seller .seller-content .overview .overview-top .rating-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.seller .seller-content .overview .overview-top .rating-wrap .rating-count,
.seller .seller-content .overview .overview-top .rating-wrap .sell-count {
  font-size: 10px;
  line-height: 18px;
  color: #4d555d;
  vertical-align: top;
}
.seller .seller-content .overview .overview-top .rating-wrap .rating-count {
  margin: 0 12px 0 8px;
}
.seller .seller-content .overview .overview-bottom {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
}
.seller .seller-content .overview .overview-bottom .item-left,
.seller .seller-content .overview .overview-bottom .item-right {
  width: 30%;
}
.seller .seller-content .overview .overview-bottom .item-center {
  width: 40%;
  border-left: 1px solid rgba(7,17,27,0.1);
  border-right: 1px solid rgba(7,17,27,0.1);
}
.seller .seller-content .overview .overview-bottom .item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  text-align: center;
}
.seller .seller-content .overview .overview-bottom .item .text {
  margin-bottom: 4px;
  font-size: 10px;
  line-height: 10px;
  color: #93999f;
}
.seller .seller-content .overview .overview-bottom .item .content {
  font-size: 24px;
  line-height: 24px;
  color: #07111b;
}
.seller .seller-content .overview .overview-bottom .item .content i {
  font-size: 10px;
}
.seller .seller-content .overview .collect {
  position: absolute;
  top: 18px;
  right: 18px;
  width: 36px;
  text-align: center;
}
.seller .seller-content .overview .collect span {
  display: block;
}
.seller .seller-content .overview .collect .icon-favorite {
  margin-bottom: 4px;
  font-size: 24px;
  line-height: 24px;
  color: #d6d4d9;
}
.seller .seller-content .overview .collect .text {
  font-size: 10px;
  line-height: 10px;
  color: #d6d4d9;
}
.seller .seller-content .overview .collect.active .icon-favorite {
  color: #f01414;
}
.seller .seller-content .overview .collect.active .text {
  color: #4d555d;
}
.seller .seller-content .desc {
  padding: 18px 18px 0px 18px;
}
.seller .seller-content .desc .title {
  margin: 0px 0px 12px 0px;
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
}
.seller .seller-content .desc .bulletin {
  margin: 0px 12px 16px 12px;
  font-size: 12px;
  line-height: 24px;
  color: #f01414;
  letter-spacing: 2px;
}
.seller .seller-content .desc .supports .support {
  padding: 16px 12px;
  border-top: 1px solid rgba(7,17,27,0.1);
}
.seller .seller-content .desc .supports .support .decrease {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url("./images/decrease_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .seller-content .desc .supports .support .decrease {
    background-image: url("./images/decrease_4@3x.png");
}
}
.seller .seller-content .desc .supports .support .discount {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url("./images/discount_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .seller-content .desc .supports .support .discount {
    background-image: url("./images/discount_4@3x.png");
}
}
.seller .seller-content .desc .supports .support .guarantee {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url("./images/guarantee_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .seller-content .desc .supports .support .guarantee {
    background-image: url("./images/guarantee_4@3x.png");
}
}
.seller .seller-content .desc .supports .support .invoice {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url("./images/invoice_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .seller-content .desc .supports .support .invoice {
    background-image: url("./images/invoice_4@3x.png");
}
}
.seller .seller-content .desc .supports .support .special {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url("./images/special_4@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.seller .seller-content .desc .supports .support .special {
    background-image: url("./images/special_4@3x.png");
}
}
.seller .seller-content .desc .supports .support .icon {
  display: inline-block;
  margin-right: 6px;
  width: 16px;
  height: 16px;
  vertical-align: top;
}
.seller .seller-content .desc .supports .support .text {
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
}
.seller .seller-content .image {
  padding: 18px;
}
.seller .seller-content .image .title {
  margin: 0px 0px 12px 0px;
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
}
.seller .seller-content .image .image-wrap {
  width: 100%;
  overflow: hidden;
  font-size: 0px;
}
.seller .seller-content .image .image-wrap .image-list {
  display: table;
  white-space: nowrap;
}
.seller .seller-content .image .image-wrap .image-list .item + .item {
  margin-left: 6px;
}
.seller .seller-content .image .image-wrap .image-list .item {
  display: inline-block;
  width: 120px;
  height: 90px;
}
.seller .seller-content .image .image-wrap .image-list .item .item-img {
  width: 100%;
  height: 100%;
}
.seller .seller-content .infos {
  padding: 18px 18px 0px 18px;
}
.seller .seller-content .infos .title {
  margin: 0px 0px 12px 0px;
  font-size: 14px;
  line-height: 14px;
  color: #07111b;
}
.seller .seller-content .infos .info-list .item {
  padding: 16px 12px;
  border-top: 1px solid rgba(7,17,27,0.1);
  font-size: 12px;
  line-height: 16px;
  color: #07111b;
}


