
.rating-select {
  padding: 0 18px;
  position: relative;
}
.rating-select:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  content: '';
  border-top: 1px solid rgba(7,17,27,0.1);
}
.rating-select .select-type {
  padding-bottom: 18px;
  position: relative;
}
.rating-select .select-type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  content: '';
  border-top: 1px solid rgba(7,17,27,0.1);
}
.rating-select .select-type .btn {
  margin-right: 8px;
  padding: 8px 12px;
  font-size: 12px;
  line-height: 16px;
  color: #4d555d;
  border-radius: 2px;
}
.rating-select .select-type .btn:last-child {
  margin-right: 0px;
}
.rating-select .select-type .btn .count {
  font-size: 8px;
  margin-left: 4px;
}
.rating-select .select-type .all,
.rating-select .select-type .positive {
  background-color: rgba(0,160,220,0.2);
}
.rating-select .select-type .all.active,
.rating-select .select-type .positive.active {
  color: #fff;
  background-color: #00a0dc;
}
.rating-select .select-type .negative {
  background-color: rgba(77,85,93,0.2);
}
.rating-select .select-type .negative.active {
  color: #fff;
  background-color: #4d555d;
}
.rating-select .only-content {
  padding: 12px 0;
}
.rating-select .only-content .icon-check_circle {
  margin-right: 4px;
  font-size: 24px;
  line-height: 24px;
  color: #93999f;
  vertical-align: top;
}
.rating-select .only-content .icon-check_circle.active {
  color: #00c850;
}
.rating-select .only-content .text {
  font-size: 12px;
  line-height: 24px;
  color: #93999f;
  vertical-align: top;
}


