
.cartcontrol {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cartcontrol .decrease,
.cartcontrol .add {
  padding: 6px;
  text-align: center;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
  vertical-align: top;
}
.cartcontrol .count {
  width: 24px;
  text-align: center;
  font-size: 10px;
  line-height: 36px;
  color: #93999f;
}
.cartcontrol .decrease .icon-remove_circle_outline {
  display: block /*块级元素才有旋转的概念*/;
}
.cartcontrol .decrease.fade-enter-active,
.cartcontrol .decrease.fade-leave-active {
  transition: all 0.4s linear;
}
.cartcontrol .decrease.fade-enter-active .icon-remove_circle_outline,
.cartcontrol .decrease.fade-leave-active .icon-remove_circle_outline {
  transition: all 0.4s linear;
}
.cartcontrol .decrease.fade-enter,
.cartcontrol .decrease.fade-leave-active {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
  opacity: 0;
}
.cartcontrol .decrease.fade-enter .icon-remove_circle_outline,
.cartcontrol .decrease.fade-leave-active .icon-remove_circle_outline {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}


