
.food {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 48px;
  z-index: 40;
  overflow: hidden;
  background-color: #fff;
}
.food.fade-enter-active,
.food.fade-leave-active {
  transition: all 0.2s linear;
}
.food.fade-enter,
.food.fade-leave-active {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
}
.food .food-content .header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .food-content .header .image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .food-content .header .btn-close {
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 6px 8px 6px 6px;
  border-radius: 50%;
  background-color: rgba(7,17,27,0.6);
}
.food .food-content .header .btn-close .icon-arrow_lift {
  font-size: 18px;
  color: #fff;
}
.food .food-content .info {
  position: relative;
  padding: 18px;
}
.food .food-content .info .name {
  margin: 0 0 8px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  color: #07111b;
}
.food .food-content .info .count-rating {
  margin-bottom: 18px;
}
.food .food-content .info .count-rating .count,
.food .food-content .info .count-rating .rating {
  font-size: 10px;
  line-height: 10px;
  color: #93999f;
}
.food .food-content .info .count-rating .count {
  margin-right: 12px;
}
.food .food-content .info .price-wrap .price {
  margin-right: 8px;
  font-size: 14px;
  font-weight: 700;
  line-height: 24px;
  color: #f01414;
}
.food .food-content .info .price-wrap .price i {
  font-size: 10px;
  font-weight: 200;
}
.food .food-content .info .price-wrap .old-price {
  font-size: 10px;
  font-weight: 700;
  line-height: 24px;
  color: #93999f;
  text-decoration: line-through;
}
.food .food-content .info .price-wrap .old-price i {
  font-weight: 200;
}
.food .food-content .info .add-cart {
  position: absolute;
  right: 18px;
  bottom: 18px;
  padding: 6px 12px;
  font-size: 10px;
  line-height: 12px;
  color: #fff;
  background-color: #00a0dc;
  border-radius: 24px;
}
.food .food-content .info .add-cart.btn-fade-enter-active,
.food .food-content .info .add-cart.btn-fade-leave-active {
  transition: all 0.2s linear;
}
.food .food-content .info .add-cart.btn-fade-enter,
.food .food-content .info .add-cart.btn-fade-leave-active {
  opacity: 0;
  -webkit-transform: scale(2, 0);
          transform: scale(2, 0);
}
.food .food-content .info .control-wrap {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
.food .food-content .desc-wrap {
  padding: 18px;
}
.food .food-content .desc-wrap .title {
  margin: 0 0 6px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  color: #07111b;
}
.food .food-content .desc-wrap .desc {
  margin: 0 8px;
  font-size: 12px;
  line-height: 24px;
  color: #4d555d;
  letter-spacing: 2px;
}
.food .food-content .rating-wrap .title {
  margin: 18px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  color: #07111b;
}
.food .food-content .rating-wrap .rating-list {
  padding: 0 18px;
}
.food .food-content .rating-wrap .rating-list .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food .food-content .rating-wrap .rating-list .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  content: '';
  border-top: 1px solid rgba(7,17,27,0.1);
}
.food .food-content .rating-wrap .rating-list .rating-item:last-child:after {
  display: none;
}
.food .food-content .rating-wrap .rating-list .rating-item .time {
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
}
.food .food-content .rating-wrap .rating-list .rating-item .rating-content {
  margin-top: 6px;
}
.food .food-content .rating-wrap .rating-list .rating-item .rating-content .icon {
  margin-right: 4px;
  font-size: 12px;
  line-height: 24px;
  vertical-align: top;
}
.food .food-content .rating-wrap .rating-list .rating-item .rating-content .icon.icon-thumb_up {
  color: #00a0dc;
}
.food .food-content .rating-wrap .rating-list .rating-item .rating-content .icon.icon-thumb_down {
  color: #93999f;
}
.food .food-content .rating-wrap .rating-list .rating-item .rating-content .text {
  font-size: 12px;
  line-height: 22px;
  color: #07111b;
  vertical-align: top;
}
.food .food-content .rating-wrap .rating-list .rating-item .user {
  position: absolute;
  top: 16px;
  right: 0;
}
.food .food-content .rating-wrap .rating-list .rating-item .user .username {
  margin-right: 6px;
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
  vertical-align: top;
}
.food .food-content .rating-wrap .rating-list .rating-item .user .avatar {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  vertical-align: top;
}
.food .food-content .rating-wrap .not-rating {
  padding: 16px 18px;
  font-size: 12px;
  color: #93999f;
}


